'use client'

import React, { useState, useEffect, useRef, useCallback } from 'react';
import { useRouter } from 'next/navigation';
import { v4 as uuidv4 } from 'uuid';
import { createArticle, updateArticle, getArticleById, Article } from '@/firebase/articles';
import { auth } from '@/firebase/clientApp';
import Editor from './Editor';
import InspirationWidget from './InspirationWidget';
import styles from '@/styles/home.module.css';
import { User } from 'firebase/auth';

interface ArticleComposerProps {
  articleId?: string; // Optional - if provided, edit mode, otherwise create mode
  onUpdateComplete?: (updatedArticle?: Article | null) => void; // Callback when update is complete
  backToArticleAction?: () => void; // Action to go back to article view
  editingTitle?: string; // Title for editing mode display
}

const ArticleComposer: React.FC<ArticleComposerProps> = ({ articleId, onUpdateComplete, backToArticleAction, editingTitle }) => {
  const router = useRouter();
  const [title, setTitle] = useState('');
  const [content, setContent] = useState('');
  const [tags, setTags] = useState<string[]>([]);
  const [coverImage, setCoverImage] = useState('');
  const [isPublishing, setIsPublishing] = useState(false);
  const [isSaving, setIsSaving] = useState(false);
  const [isSaved, setIsSaved] = useState(false);
  const [error, setError] = useState('');
  const [isLoading, setIsLoading] = useState(!!articleId);
  const [user, setUser] = useState<User | null>(null);
  const [currentTag, setCurrentTag] = useState('');
  
  const titleRef = useRef<HTMLTextAreaElement>(null);
  const tagInputRef = useRef<HTMLInputElement>(null);
  const editorRef = useRef<any>(null);
  
  // Auto-resize title textarea as content grows
  const autoResizeTitle = useCallback(() => {
    if (titleRef.current) {
      titleRef.current.style.height = 'auto';
      titleRef.current.style.height = `${titleRef.current.scrollHeight}px`;
      // Set focus to title if it's empty when component loads
      if (!title && titleRef.current) {
        titleRef.current.focus();
      }
    }
  }, [title]);
  
  // Set up authentication listener
  useEffect(() => {
    const unsubscribe = auth.onAuthStateChanged(user => {
      setUser(user);
      if (!user) {
        // Redirect to login if not authenticated
        router.push('/login?redirect=compose');
      }
    });
    
    return () => unsubscribe();
  }, [router]);
  
  // Load article data if in edit mode
  useEffect(() => {
    if (process.env.NODE_ENV === 'development') {
      console.log('ArticleComposer useEffect - articleId:', articleId, 'user:', user ? '[USER_LOGGED_IN]' : 'undefined');
    }
    if (articleId && user) {
      const fetchArticle = async () => {
        try {
          if (process.env.NODE_ENV === 'development') {
            console.log('Fetching article for editing, ID:', articleId);
          }
          setIsLoading(true);
          const article = await getArticleById(articleId);
          
          if (process.env.NODE_ENV === 'development' && article) {
            console.log('Fetched article:', article.title, 'Body length:', article.body?.length);
          }
          
          if (!article) {
            if (process.env.NODE_ENV === 'development') {
              console.log('Article not found');
            }
            setError('Article not found');
            router.push('/');
            return;
          }
          
          // Verify article ownership
          if (article.authorId !== user.uid) {
            if (process.env.NODE_ENV === 'development') {
              console.log('User not authorized to edit this article');
            }
            setError('You can only edit your own articles');
            router.push('/');
            return;
          }
          
          // Set form data
          if (process.env.NODE_ENV === 'development') {
            console.log('Setting title:', article.title);
          }
          setTitle(article.title);
          
          // Process HTML to extract and clean the content
          const cleanContent = article.body.replace(/data-[\w-]+="[^"]*"/g, '');
          if (process.env.NODE_ENV === 'development') {
            console.log('Setting content, length:', cleanContent.length, 'Preview:', cleanContent.substring(0, 100) + '...');
          }
          setContent(cleanContent);
          
          setTags(article.tags || []);
          setCoverImage(article.coverImage || '');
          
          if (process.env.NODE_ENV === 'development') {
            console.log('Article data loaded successfully for editing');
          }
        } catch (error) {
          console.error('Error loading article:', error);
          setError('Failed to load article');
        } finally {
          setIsLoading(false);
        }
      };
      
      fetchArticle();
    } else if (!articleId) {
      if (process.env.NODE_ENV === 'development') {
        console.log('No articleId, starting with empty content');
      }
      // Editor will start with empty content - no need for document model
      setIsLoading(false);
    } else {
      if (process.env.NODE_ENV === 'development') {
        console.log('User not logged in, waiting...');
      }
    }
  }, [articleId, user, router]);
  
  // Auto-resize title when it changes
  useEffect(() => {
    autoResizeTitle();
  }, [title, autoResizeTitle]);
  
  // Auto-save draft periodically (every 30 seconds)
  useEffect(() => {
    let timer: NodeJS.Timeout;
    
    if (user && title.trim() && content.trim() && !isSaving && !isPublishing) {
      timer = setTimeout(() => {
        saveArticle('drafts');
      }, 30000);
    }
    
    return () => {
      if (timer) clearTimeout(timer);
    };
  }, [user, title, content, isSaving, isPublishing]);
  
  // Handle title changes
  const handleTitleChange = (e: React.ChangeEvent<HTMLTextAreaElement>) => {
    setTitle(e.target.value);
  };
  
  // Handle editor content changes with debouncing for better performance
  const handleEditorChange = useCallback((html: string, json: any) => {
    // Use functional update to avoid dependency on current content
    setContent(prevContent => {
      // Only update if content has actually changed
      if (html !== prevContent) {
        // Reset saved status when content changes
        if (isSaved) {
          setIsSaved(false);
        }
        return html;
      }
      return prevContent;
    });
  }, [isSaved]);
  
  // Handle tag input
  const handleTagInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setCurrentTag(e.target.value);
  };
  
  // Handle tag input keydown
  const handleTagInputKeyDown = (e: React.KeyboardEvent<HTMLInputElement>) => {
    if (e.key === 'Enter' || e.key === ',') {
      e.preventDefault();
      addTag();
    }
  };
  
  // Add a tag
  const addTag = () => {
    const tag = currentTag.trim().toLowerCase();
    if (tag && !tags.includes(tag) && tags.length < 5) {
      setTags([...tags, tag]);
      setCurrentTag('');
    }
  };
  
  // Remove a tag
  const removeTag = (tagToRemove: string) => {
    setTags(tags.filter(tag => tag !== tagToRemove));
  };
  
  // Handle cover image input
  const handleCoverImageChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setCoverImage(e.target.value);
  };
  
  // Handle focus moving from title to editor
  const handleTitleKeyDown = (e: React.KeyboardEvent<HTMLTextAreaElement>) => {
    // When user presses Enter in the title field, move focus to the editor
    if (e.key === 'Enter') {
      e.preventDefault();
      // Note: The new Editor component will handle its own focus
      // when the user starts typing in the editor area
    }
  };
  
  // Save article function
  const saveArticle = async (status: 'drafts' | 'published' = 'drafts') => {
    if (!user) {
      setError('You must be logged in to save articles');
      return;
    }

    if (!title.trim()) {
      setError('Title is required');
      return;
    }

    try {
      setError('');
      setIsSaving(true);

      const slug = title.toLowerCase().replace(/[^a-z0-9]+/g, '-').replace(/^-|-$/g, '');
      
      const articleData = {
        title: title.trim(),
        body: content,
        tags,
        coverImage,
        status,
        authorId: user.uid,
        authorName: user.displayName || user.email || 'Anonymous',
        slug,
      };

      if (articleId) {
        // Update existing article
        try {
          if (process.env.NODE_ENV === 'development') {
            console.log('Updating article with ID:', articleId);
          }
          const updatedArticleData = await updateArticle(articleId, articleData);
          if (process.env.NODE_ENV === 'development') {
            console.log('Article updated successfully');
          }
          setIsSaved(true);
          
          if (onUpdateComplete) {
            if (process.env.NODE_ENV === 'development') {
              console.log('Calling onUpdateComplete callback instead of navigating');
            }
            onUpdateComplete(updatedArticleData);
          } else {
            // Only navigate if no callback is provided (standalone editor usage)
            if (process.env.NODE_ENV === 'development') {
              console.log('No callback provided, navigating to article page');
            }
            setTimeout(() => {
              router.push(`/articles/?slug=${slug}`);
            }, 1000);
          }
        } catch (error) {
          console.error('Error updating article:', error);
          setError('Failed to update article. Please try again.');
          setIsSaved(false);
        }
      } else {
        // Create new article
        const newArticle = await createArticle(articleData);
        if (status === 'published') {
          // Redirect to the new article page using slug
          router.push(`/articles/?slug=${slug}`);
        } else {
          // Redirect to dashboard
          router.push(`/dashboard`);
        }
      }
      
    } catch (error) {
      console.error('Error saving article:', error);
      setError('Failed to save article. Please try again.');
    } finally {
      setIsSaving(false);
      setIsPublishing(false);
    }
  };
  
  // Handle publish button click
  const handlePublish = async () => {
    if (!content.trim()) {
      setError('Content is required');
      return;
    }
    
    setIsPublishing(true);
    await saveArticle('published');
  };
  
  // Handle save draft button click
  const handleSaveDraft = async () => {
    await saveArticle('drafts');
  };

  // Handle inserting text from inspiration widget
  const handleInsertInspiration = (text: string) => {
    // Try to insert using the editor ref first
    if (editorRef.current && editorRef.current.insertText) {
      editorRef.current.insertText('\n\n' + text + '\n\n');
    } else {
      // Fallback: append to content with proper spacing
      setContent(prevContent => {
        const newContent = prevContent.trim() 
          ? prevContent + '\n\n<p>' + text + '</p>' 
          : '<p>' + text + '</p>';
        return newContent;
      });
    }
    
    // Focus the editor after insertion
    setTimeout(() => {
      if (editorRef.current && editorRef.current.focus) {
        editorRef.current.focus();
      }
    }, 100);
  };
  
  if (isLoading) {
    return (
      <div className="min-h-screen flex items-center justify-center" style={{
        background: 'linear-gradient(135deg, #f0f9ff 0%, #f7fafc 20%, #fef7ff 40%, #f0fdfa 60%, #fff7ed 80%, #f3f4f6 100%)',
        backgroundAttachment: 'fixed'
      }}>
        <div className={styles['glass-loading']}>
          <div className="w-12 h-12 border-4 border-blue-500 border-t-transparent rounded-full animate-spin mx-auto mb-4"></div>
          <p className="text-stone-600 text-lg">Loading your article...</p>
        </div>
      </div>
    );
  }
  
  return (
    <div className={`${styles['glass-container']} min-h-screen w-full`} style={{
      background: 'linear-gradient(135deg, #f0f9ff 0%, #f7fafc 20%, #fef7ff 40%, #f0fdfa 60%, #fff7ed 80%, #f3f4f6 100%)',
      backgroundAttachment: 'fixed',
      position: 'relative',
      borderRadius: '0px',
      border: 'none'
    }}>
      {/* Glass highlight for entire page */}
      <div className={styles['glass-highlight']} />

      {/* Floating Header */}
      <header className={`${styles['glass-container']} fixed top-6 left-6 right-6 z-50 max-w-6xl mx-auto`} style={{
        backdropFilter: 'blur(20px) saturate(180%)',
      }}>
        <div className={styles['glass-highlight']} />
        
        <div className={`${styles['glass-content']} flex items-center justify-between p-4`}>
          <div className="flex items-center gap-4">
            {backToArticleAction ? (
              <button
                onClick={backToArticleAction}
                className={`${styles['glass-button']} flex items-center gap-2 px-3 py-2 text-sm font-medium text-stone-700 hover:text-stone-900 transition-colors`}
              >
                <svg width="16" height="16" viewBox="0 0 24 24" fill="currentColor">
                  <path d="M20 11H7.83l5.59-5.59L12 4l-8 8 8 8 1.42-1.41L7.83 13H20v-2z" />
                </svg>
                Back to Article
              </button>
            ) : (
              <div className="text-xl font-bold text-stone-800 font-serif">Journalite</div>
            )}
            
            <div className="text-stone-500 text-sm">
              {articleId ? (editingTitle ? `Editing: ${editingTitle}` : 'Editing') : 'Writing'}
            </div>
            
            {isSaved && (
              <div className="flex items-center gap-2 text-green-600 text-sm">
                <svg viewBox="0 0 24 24" width="16" height="16" fill="currentColor">
                  <path d="M9 16.17L4.83 12l-1.42 1.41L9 19 21 7l-1.41-1.41L9 16.17z" />
                </svg>
                Saved
              </div>
            )}
          </div>
          
          <div className="flex items-center gap-3">
            <button
              className={`${styles['glass-button']} px-4 py-2 text-sm font-semibold disabled:opacity-50`}
              onClick={handleSaveDraft}
              disabled={isSaving || isPublishing}
            >
              {isSaving ? 'Saving...' : 'Save Draft'}
            </button>
            
            <button
              className={`${styles['glass-button']} ${styles['glass-button-primary']} px-4 py-2 text-sm font-semibold disabled:opacity-50`}
              onClick={handlePublish}
              disabled={isSaving || isPublishing || !title.trim() || !content.trim()}
            >
              {isPublishing ? 'Publishing...' : (articleId ? 'Update' : 'Publish')}
            </button>
          </div>
        </div>
      </header>
      
      {/* Glass content wrapper for seamless editor with Medium-style layout */}
      <div className={`${styles['glass-content']} pt-32 pb-16 min-h-screen w-full max-w-4xl mx-auto`} style={{ paddingLeft: '64px', paddingRight: '64px' }}>
        {/* Title - completely seamless */}
        <textarea
          ref={titleRef}
          value={title}
          onChange={handleTitleChange}
          onKeyDown={handleTitleKeyDown}
          placeholder="Title"
          rows={1}
          className="w-full bg-transparent border-none outline-none resize-none overflow-hidden text-5xl font-bold text-stone-800 placeholder-stone-400 font-serif leading-tight mb-8 focus:outline-none"
          style={{
            minHeight: '60px'
          }}
        />

        {/* Cover Image - seamless integration */}
        {(coverImage || title.length > 10) && (
          <div className="mb-8">
            {!coverImage && (
              <input
                type="text"
                value={coverImage}
                onChange={handleCoverImageChange}
                placeholder="Add a cover image URL..."
                className="w-full bg-transparent border-none outline-none text-stone-600 placeholder-stone-400 py-2 border-b border-stone-200 focus:border-stone-400 transition-colors"
              />
            )}
            {coverImage && (
              <div className="relative group">
                <img 
                  src={coverImage} 
                  alt="Cover" 
                  className="w-full h-64 object-cover rounded-2xl mb-4"
                />
                <input
                  type="text"
                  value={coverImage}
                  onChange={handleCoverImageChange}
                  placeholder="Cover image URL..."
                  className="w-full bg-white/70 backdrop-blur border border-stone-200 outline-none text-stone-600 placeholder-stone-400 py-1.5 px-3 rounded-md focus:border-stone-400 transition-colors"
                />
              </div>
            )}
          </div>
        )}

        {/* Tags - seamless inline */}
        {(tags.length > 0 || title.length > 5) && (
          <div className="mb-8 flex flex-wrap gap-2 items-center">
            {tags.map(tag => (
              <div key={tag} className="px-2.5 py-0.5 bg-blue-100/50 backdrop-blur text-blue-700 rounded-full text-sm font-medium flex items-center gap-1.5">
                {tag}
                <button 
                  className="text-blue-600 hover:text-blue-800 font-bold text-base leading-none"
                  onClick={() => removeTag(tag)}
                  aria-label={`Remove ${tag} tag`}
                >
                  ×
                </button>
              </div>
            ))}
            
            {tags.length < 5 && (
              <input
                ref={tagInputRef}
                type="text"
                value={currentTag}
                onChange={handleTagInputChange}
                onKeyDown={handleTagInputKeyDown}
                onBlur={addTag}
                placeholder={tags.length === 0 ? "Add tags..." : "Add tag..."}
                className="bg-transparent border-none outline-none text-stone-600 placeholder-stone-400 min-w-24 py-1"
              />
            )}
          </div>
        )}

        {/* Error Message */}
        {error && (
          <div className="mb-6 p-4 bg-red-50/80 border border-red-200 rounded-xl text-red-700 text-sm">
            {error}
          </div>
        )}
        
        {/* Article Editor - completely seamless, full width */}
        <div className="min-h-[500px] w-full" style={{
          background: 'transparent'
        }}>
          <Editor
            ref={editorRef}
            {...({
              articleId: articleId || uuidv4(),
              initialContent: content,
              onChange: handleEditorChange,
              placeholder: "Write your story...",
              className: "seamless-editor"
            } as any)}
          />
        </div>
      </div>

      {/* Inspiration Widget */}
      <InspirationWidget onInsert={handleInsertInspiration} />
    </div>
  );
};

export default ArticleComposer; 